*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
  background-color: #1b1b32;
  color:white;
  /* text-shadow: 0 0 .5rem; */
}
body{
  width: 100%;
  font-size: 1.125rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

h1{
  max-width: 58.75rem;
  margin: 2.5rem 0;
  font-size: 2.5rem;
  text-align: center;
  font-weight: 700;
  line-height: 1.1;  
}

.App{
  min-height: 100vh;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.contenedor-principal{
  /* width: 100%; */
  min-height: 37.51rem;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  /* border-style: solid; */
  flex-direction: column;
}

img{
  height: 100%;
}

@media (max-width: 720px) {
  body{
    width: 100%;
    display: flex;
    flex-direction: column;
  }
 
  .contenedor-principal{
    width: 100%;
  }
  .App{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  h1{
    margin: 1rem;
    font-size: 2rem;
  }
}

@media (max-width: 1280px) {
  body{
    width: 100%;
    
    
  }
 
  .contenedor-principal{
    width: 100%;
  }
  .App{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  h1{
    margin: 1rem;
    font-size: 2rem;
    text-align: center;
  }
}