.root{
    width: 75%;
    display: flex;
    align-items: center;
}

.contenedor-testimonio{
    width: 73.125rem;
    height: 23rem;
    margin: 0.625rem 0.625rem 3.125rem;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 0 .5rem black;
    border-radius: .5rem;
    -webkit-border-radius: .1rem;
    -moz-border-radius: .5rem;
    -ms-border-radius: .5rem;
    -o-border-radius: .5rem;
    /* border-style: solid; */
}

.contenedor-testimonio,
.nombre-testimonio,
.cargo-testimonio,
.texto-testimonio{
    background-color: #0a0a23;
}

.texto-testimonio{
    padding: 2.5rem;
    font-weight: 400;
    line-height: 1.5rem;
    font-size: 1.1rem;  
    text-align:justify;
    /* border-style: solid; */
}

.nombre-testimonio{
    font-size: 1.3rem;
    /* border-style: solid; */
    height: 3rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.cargo-testimonio{
    height:3rem;
    font-size: 1.3rem;
    background-color: #0a0a23;
    display: flex;
    align-items: center;
    justify-content: center;
    /* border-style: solid; */
}

.imagen-testimonio{
    border-radius: .3rem 0 0 .3rem;
    -webkit-border-radius: .3rem 0 0 .3rem;
    -moz-border-radius: .3rem 0 0 .3rem;
    -ms-border-radius: .3rem 0 0 .3rem;
    -o-border-radius: .3rem 0 0 .3rem;
}

@media screen and (max-width: 720px) {
   *{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
   }
   html,
   body{
    width: 100%;
   }
   .root{
    width: 99%;
   }
   .contenedor-testimonio{
    width: auto;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    }
    .imagen-testimonio{
        width: 100%;
    }
    .imagen-testimonio{
        border-radius: .3rem .3rem 0 0;
        -webkit-border-radius: .3rem .3rem 0 0;
        -moz-border-radius: .3rem .3rem 0 0;
        -ms-border-radius: .3rem .3rem 0 0;
        -o-border-radius: .3rem .3rem 0 0;
}
    
}

@media screen and (max-width: 1280px) {
    *{
     margin: 0;
     padding: 0;
     box-sizing: border-box;
    }
    html,
    body{
     width: 100%;
    }
    .root{
     width: 100%;
    }
    .contenedor-testimonio{
     width: 95%;
     height: auto;
     display: flex;
     align-items: center;
     justify-content: center;
     }
     .imagen-testimonio{
         max-width: 100%;
     }

 }